// 自适应正则脚本
// 功能：检测AI输出聊天中存在表达式内容时，自动注册相应的正则规则
// 卸载时自动移除所有其注册的正则
// 性能优化：缓存检测模式、延迟执行，防抖、跳过已注册规则

import regexData from './regex.json';

$(async () => {
  console.info('自适应正则脚本已加载');

  // ===== 优化1: 使用 Set 替代数组存储已注册ID (O(1) 查找) =====
  const registeredRegexIds = new Set<string>();

  // ===== 优化2: 缓存解析结果 =====
  // 缓存提取的检测模式和预检查标记
  let cachedPatterns: { id: string; pattern: RegExp; name: string; quickCheck: string }[] | null = null;
  // 缓存转换后的酒馆正则规则
  let cachedTavernRules: TavernRegex[] | null = null;

  // 解析正则数据，转换为酒馆正则格式（带缓存）
  const parseRegexRules = (): TavernRegex[] => {
    if (cachedTavernRules) {
      return cachedTavernRules;
    }

    cachedTavernRules = regexData
      .filter((rule: any) => !rule.disabled)
      .map((rule: any) => ({
        id: rule.id,
        script_name: rule.scriptName,
        enabled: true,
        scope: 'character' as const,
        find_regex: rule.findRegex,
        replace_string: rule.replaceString,
        trim_strings: Array.isArray(rule.trimStrings) ? rule.trimStrings.join('\n') : '',
        source: {
          user_input: false,
          ai_output: true,
          slash_command: false,
          world_info: false,
        },
        destination: {
          display: true,
          prompt: false,
        },
        run_on_edit: rule.runOnEdit ?? true,
        min_depth: rule.minDepth ?? null,
        max_depth: rule.maxDepth ?? 10,
      }));

    return cachedTavernRules;
  };

  // 从正则规则中提取检测模式（带缓存）
  const extractDetectionPatterns = (): { id: string; pattern: RegExp; name: string; quickCheck: string }[] => {
    if (cachedPatterns) {
      return cachedPatterns;
    }

    cachedPatterns = regexData
      .filter((rule: any) => !rule.disabled)
      .map((rule: any) => {
        // 清理正则表达式
        let patternStr = rule.findRegex;
        const match = patternStr.match(/^\/(.+)\/([gimsuy]*)$/);
        if (match) {
          patternStr = match[1];
        }

        // 尝试创建正则
        try {
          const pattern = new RegExp(patternStr, 'i');

          // ===== 优化3: 改进快速检查字符串提取 =====
          let quickCheck = '';
          // 优先提取正则中的特殊字符或结构化标记
          if (patternStr.includes('{') || patternStr.includes('\\{')) {
            quickCheck = '{';
          } else if (patternStr.includes('<') || patternStr.includes('\\<')) {
            const tagMatch = patternStr.match(/<(\w+)/);
            if (tagMatch) {
              quickCheck = `<${tagMatch[1]}`;
            }
          } else if (patternStr.includes('\\[') || patternStr.includes('\\]')) {
            quickCheck = '[';
          } else if (patternStr.includes('\\(') || patternStr.includes('\\)')) {
            quickCheck = '(';
          } else if (patternStr.startsWith('>')) {
            // 中文冒号格式
            quickCheck = '>';
          } else if (patternStr.startsWith('<')) {
            // 标签格式
            const firstTagMatch = patternStr.match(/<(\w+)/);
            if (firstTagMatch) {
              quickCheck = `<${firstTagMatch[1]}`;
            }
          } else {
            // 使用正则的前15个字符作为快速检查
            quickCheck = patternStr.substring(0, Math.min(15, patternStr.length));
          }

          return {
            id: rule.id,
            pattern,
            name: rule.scriptName,
            quickCheck,
          };
        } catch (e) {
          console.warn(`无效的正则表达式: ${patternStr}`, e);
          return null;
        }
      })
      .filter(Boolean) as { id: string; pattern: RegExp; name: string; quickCheck: string }[];

    return cachedPatterns;
  };

  // ===== 优化10: 酒馆正则已注册名称缓存 =====
  const existingRegexNames = new Set<string>();

  // ===== 聊天变量键名 =====
  const CHAT_VAR_KEY = '自适应正则_已注册列表';

  // ===== 优化: 跟踪每个消息的最后检测时间 =====
  const messageLastDetectTime = new Map<number, number>();
  const DETECT_COOLDOWN = 2000; // 2秒内不重复检测同一消息

  // 从聊天变量加载已注册的正则名称
  const loadRegisteredRegexesFromChat = (): string[] => {
    try {
      const chatVars = getVariables({ type: 'chat' });
      const registered = chatVars?.[CHAT_VAR_KEY];
      if (Array.isArray(registered)) {
        return registered.filter((item): item is string => typeof item === 'string');
      }
    } catch (e) {
      // 忽略读取错误
    }
    return [];
  };

  // 保存已注册的正则名称到聊天变量
  const saveRegisteredRegexesToChat = (names: string[]) => {
    try {
      replaceVariables({ [CHAT_VAR_KEY]: names }, { type: 'chat' });
    } catch (e) {
      // 忽略保存错误
    }
  };

  // 初始化时获取已存在的正则名称（从酒馆和聊天变量）
  const initExistingRegexes = async () => {
    try {
      // 1. 获取酒馆中已存在的正则名称
      await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
        regexes.forEach(r => existingRegexNames.add(r.script_name));
        return regexes;
      });

      // 2. 从聊天变量加载已注册的正则名称（用于跨会话保持）
      const savedNames = loadRegisteredRegexesFromChat();
      savedNames.forEach(name => existingRegexNames.add(name));
      console.info(`自适应正则: 从聊天变量恢复了 ${savedNames.length} 条已注册正则`);
    } catch (e) {
      // 忽略初始化错误
    }
  };

  // 检测消息中是否包含需要处理的表达式（带快速预检查）
  const detectNeededRegexes = (messageContent: string): string[] => {
    // 消息长度预检
    if (!messageContent || messageContent.length < 3) {
      return [];
    }

    // ===== 优化: 如果所有正则都已注册，直接返回空 =====
    const patterns = extractDetectionPatterns();
    if (registeredRegexIds.size >= patterns.length) {
      return [];
    }

    const neededIds: string[] = [];

    for (const { id, pattern, name, quickCheck } of patterns) {
      // 优化5: 使用 Set 的 has 方法 O(1) 查找 - 跳过内存中已注册的正则
      if (registeredRegexIds.has(id)) {
        continue;
      }

      // 优化10: 跳过酒馆中已存在的同名正则（可能有其他脚本注册）
      if (existingRegexNames.has(name)) {
        registeredRegexIds.add(id); // 标记为已处理，避免重复检测
        continue;
      }

      // 快速预检查
      if (quickCheck && !messageContent.includes(quickCheck)) {
        continue;
      }

      try {
        pattern.lastIndex = 0;
        if (pattern.test(messageContent)) {
          neededIds.push(id);
          console.info(`自适应正则: 检测到 ${name}，准备注册`);
        }
      } catch (e) {
        // 忽略正则测试错误
      }
    }

    return neededIds;
  };

  // ===== 优化7: 批量处理函数 =====
  let debounceTimer: ReturnType<typeof setTimeout> | null = null;
  const pendingMessageIds = new Set<number>();

  const processMessageQueue = async () => {
    if (pendingMessageIds.size === 0) return;

    const messageIds = Array.from(pendingMessageIds);
    pendingMessageIds.clear();

    // 收集所有待注册的正则ID
    const allNeededIds = new Set<string>();

    for (const message_id of messageIds) {
      try {
        // ===== 优化8: 直接获取消息内容而非遍历 =====
        const messages = getChatMessages(message_id);
        if (!messages || messages.length === 0) continue;

        const messageContent = messages[0].message;
        const neededIds = detectNeededRegexes(messageContent);
        neededIds.forEach(id => allNeededIds.add(id));
      } catch (error) {
        console.error('自适应正则处理出错:', error);
      }
    }

    // 批量注册所有需要的正则
    if (allNeededIds.size > 0) {
      await registerMultipleRegexes(Array.from(allNeededIds));
    }
  };

  // 防抖包装函数
  const debouncedProcess = (message_id: number) => {
    // ===== 优化: 冷却时间检查，避免快速重复处理同一消息 =====
    const now = Date.now();
    const lastDetect = messageLastDetectTime.get(message_id) || 0;
    if (now - lastDetect < DETECT_COOLDOWN) {
      return; // 2秒内不重复处理
    }
    messageLastDetectTime.set(message_id, now);

    pendingMessageIds.add(message_id);

    if (debounceTimer) {
      clearTimeout(debounceTimer);
    }

    // 150ms 防抖延迟
    debounceTimer = setTimeout(() => {
      processMessageQueue();
    }, 150);
  };

  // 注册单个正则规则（内部使用）
  const registerSingleRegex = (regex: TavernRegex): boolean => {
    if (registeredRegexIds.has(regex.id)) {
      return false; // 已经注册过了
    }

    registeredRegexIds.add(regex.id);
    return true;
  };

  // ===== 优化9: 批量注册 - 减少 await 调用次数 =====
  const registerMultipleRegexes = async (ids: string[]): Promise<void> => {
    const allRules = parseRegexRules();
    const rulesToRegister: TavernRegex[] = [];

    for (const id of ids) {
      const rule = allRules.find((r: TavernRegex) => r.id === id);
      if (rule && registerSingleRegex(rule)) {
        rulesToRegister.push(rule);
      }
    }

    if (rulesToRegister.length === 0) return;

    // 获取已存在的正则，合并后一次性更新
    await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
      // 移除可能存在的同名正则
      const registeredNames = rulesToRegister.map(r => r.script_name);
      const filtered = regexes.filter((r: TavernRegex) => !registeredNames.includes(r.script_name));
      return [...filtered, ...rulesToRegister];
    });

    // 保存到聊天变量，便于下次加载时恢复
    const allRegisteredNames = [...loadRegisteredRegexesFromChat(), ...rulesToRegister.map(r => r.script_name)];
    // 去重
    const uniqueNames = [...new Set(allRegisteredNames)];
    saveRegisteredRegexesToChat(uniqueNames);

    console.info(`自适应正则: 已批量注册 ${rulesToRegister.length} 条规则，并保存到聊天变量`);
  };

  // 移除所有已注册的正则
  const unregisterAllRegexes = async (): Promise<void> => {
    if (registeredRegexIds.size === 0) {
      return;
    }

    const allRules = parseRegexRules();
    const registeredNames = Array.from(registeredRegexIds)
      .map(id => allRules.find((r: TavernRegex) => r.id === id)?.script_name)
      .filter(Boolean) as string[];

    await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
      return regexes.filter((r: TavernRegex) => !registeredNames.includes(r.script_name));
    });

    console.info(`自适应正则: 已移除 ${registeredNames.length} 条规则`);
    registeredRegexIds.clear();
  };

  // ===== 优化: 跟踪已处理的消息ID，避免重复处理 =====
  const processedMessageIds = new Set<number>();

  // 监听消息接收事件
  eventOn(tavern_events.MESSAGE_RECEIVED, async (message_id: number) => {
    // 只处理之前未处理过的消息
    if (!processedMessageIds.has(message_id)) {
      processedMessageIds.add(message_id);
      debouncedProcess(message_id);
    }
  });

  // 监听消息编辑事件 - 已禁用，因为会导致频繁刷新

  // ===== 监听聊天切换事件 =====
  // 使用一个标志来防止在同步过程中重复触发
  let isSyncingChat = false;
  eventOn(tavern_events.CHAT_CHANGED, async (chat_file_name: string) => {
    // 防止重复触发
    if (isSyncingChat) {
      console.info('自适应正则: 正在同步中，跳过此次触发');
      return;
    }
    isSyncingChat = true;

    console.info(`自适应正则: 聊天切换到 ${chat_file_name}，开始同步正则状态`);

    // 获取聊天变量中记录的已注册正则名称
    const savedNames = loadRegisteredRegexesFromChat();
    console.info(`自适应正则: 聊天变量中有 ${savedNames.length} 条已注册正则`);

    try {
      // 获取当前酒馆中实际已注册的正则
      const allRules = parseRegexRules();
      let actualRegisteredNames: string[] = [];

      await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
        actualRegisteredNames = regexes.filter(r => savedNames.includes(r.script_name)).map(r => r.script_name);
        return regexes;
      });

      console.info(`自适应正则: 酒馆中实际有 ${actualRegisteredNames.length} 条正则`);

      // 需要重新注册的（变量中有但正则中没有）
      const toReRegister = savedNames.filter(name => !actualRegisteredNames.includes(name));
      // 需要移除的（变量中没有但正则中有）- 这里不需要主动移除，因为脚本会在卸载时清理

      if (toReRegister.length > 0) {
        console.info(`自适应正则: 需要重新注册 ${toReRegister.length} 条正则: ${toReRegister.join(', ')}`);
        // 重新注册缺失的正则
        const rulesToReRegister = allRules.filter((r: TavernRegex) => toReRegister.includes(r.script_name));
        if (rulesToReRegister.length > 0) {
          await window.TavernHelper.updateTavernRegexesWith((regexes: TavernRegex[]) => {
            const registeredNames = rulesToReRegister.map(r => r.script_name);
            const filtered = regexes.filter((r: TavernRegex) => !registeredNames.includes(r.script_name));
            return [...filtered, ...rulesToReRegister];
          });
          console.info(`自适应正则: 已重新注册 ${rulesToReRegister.length} 条规则`);
        }
      }

      // 将实际注册的名称同步到 existingRegexNames 缓存
      actualRegisteredNames.forEach(name => existingRegexNames.add(name));
      toReRegister.forEach(name => existingRegexNames.add(name));
    } catch (error) {
      console.error('自适应正则: 聊天切换时同步出错:', error);
    }

    // 检测最新消息 - 已禁用，因为新消息会有 MESSAGE_RECEIVED 事件处理
    // try {
    //   const lastMessageId = getLastMessageId();
    //   if (lastMessageId >= 0) {
    //     const messages = getChatMessages(lastMessageId);
    //     if (messages && messages.length > 0) {
    //       const msg = messages[0];
    //       if (msg?.message_id !== undefined) {
    //         debouncedProcess(msg.message_id);
    //       }
    //     }
    //   }
    // } catch (error) {
    //   console.error('自适应正则: 检测最新消息出错:', error);
    // }

    // 重置同步标志
    isSyncingChat = false;

    // ===== 优化: 聊天切换时清空已处理消息列表 =====
    processedMessageIds.clear();
    messageLastDetectTime.clear();
  });

  const enabledRules = regexData.filter((r: any) => !r.disabled).length;
  console.info(`自适应正则: 准备了 ${enabledRules} 条规则用于检测`);

  // ===== 优化10: 初始化时获取酒馆中已存在的正则 =====
  await initExistingRegexes();

  // 卸载时移除所有已注册的正则
  $(window).on('pagehide', async () => {
    if (debounceTimer) {
      clearTimeout(debounceTimer);
    }
    await processMessageQueue();
    await unregisterAllRegexes();
    console.info('自适应正则脚本已卸载');
  });
});
